// SCSS Configuration for the 'Contact' page 

// Additional
@import '../../styles/main';
@import '../../styles/variables';


.unit {
  display: grid;
  padding-left: 3%;
  padding-top: 3%;
  padding-right: 3%;
  padding-bottom: 3%;
  grid-template-columns: 500px 500px;
  border-radius: 20px;
  box-shadow: rgba(0, 0, 0, 0.1) 0px 4px 6px -1px, rgba(0, 0, 0, 0.06) 0px 2px 4px -1px;
  background-color: $c-white;

  &__b1 {
    display: flex;
    justify-content: left;
    flex-direction: column;
  }

  &__b2 {
    display: flex;
    justify-content: right;
  }

  @include media("<desktop") {
    display: flex;
    flex-direction: column;
    gap: 50px;
    justify-content: center;
    grid-template-columns: 1fr;
    grid-template-rows: 2fr;
    padding-left: 10px;
    padding-right: 10px;
  }
  @include media("<phone") {
    display: flex;
    flex-direction: column;
    gap: 50px;
    justify-content: center;
    grid-template-columns: 1fr;
    grid-template-rows: 2fr;
    padding-left: 10px;
    padding-right: 10px;
  }
}

.ContactSection1 {
  font-size: 1.1em;
  line-height: 30px;

  &__u1 {
    font-family: $f-NemaBD;
  }
}

.ContactSection2 {
  font-size: 1.1em;
  line-height: 30px;

  &__u1 {
    font-family: $f-NemaBD;
  }
}

.buttonsUnit {
  margin-top: 5px;
  margin-bottom: 5px;
  display: flex;
  flex-direction: column;
  gap: 3px;
}

.btn-tg {
  width: 350px;
  cursor: pointer;
  height: 35px;
  font-size: 1em;
  color: $c-black;
  background-color: $c-white;
  text-align: left;
  border-top: none;
  border-left: none;
  border-right: none;
  border-bottom: 1px solid #DDDDDD;
  moz-transition: all .4s ease-in-out;
  -o-transition: all .4s ease-in-out;
  -webkit-transition: all .4s ease-in-out;
  transition: all .4s ease-in-out;

  &:hover {
    background-position: 100% 0;
    moz-transition: all .4s ease-in-out;
    -o-transition: all .4s ease-in-out;
    -webkit-transition: all .4s ease-in-out;
    transition: all .4s ease-in-out;
    background-color: #637081;
    background-image: linear-gradient(315deg, #637081 0%, #7c98b3 74%);
    box-shadow: rgba(0, 0, 0, 0.1) 0px 4px 6px -1px, rgba(0, 0, 0, 0.06) 0px 2px 4px -1px;
    color: $c-white;
    border: none;
  }
  &:focus {
    background-color: $c-black;
    color: $c-white;
  }
}

