// SCSS Configuration for the 'BasicCourse' page 

// Additional
@import '../../../styles/main';
@import '../../../styles/variables';


.Block {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  background-color: $c-white;
  width: 95%;
  max-width: 95%;
  height: 100%;
  color: $c-black;
  border-radius: 4em 8em;

  @include media("<desktop") {
    display: flex;
    flex-direction: column;
  }
  @include media("<phone") {
    border-radius: 0;
    width: 100%;
    max-width: 100%;
    height: 400px;
    padding-left: 1px;
    padding-right: 1px;
  }
}

.CourseLogo {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-items: center;
  background-color: #045de9;
  background-image: linear-gradient(315deg, #045de9 0%, #09c6f9 74%);
  width: 60%;
  height: 100%;
  padding-top: 10px;
  padding-bottom: 10px;
  border-radius: 4em 8em;
  opacity: 0.9;

  &__CN1 {
    font-family: $f-NfBL;
    color: $c-white;
    font-size: 2em;
  }

  &__CN2 {
    font-family: $f-NfBL;
    color: $c-white;
    font-size: 2em;
    // Removes the fill of the letter, gives only a contour
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    -webkit-text-stroke-width: 1px;
    -webkit-text-stroke-color: $c-white;
  }
}

.section1 {
  margin-top: 2%;
  margin-bottom: 2%;
  border-radius: 20px;
  box-shadow: rgba(0, 0, 0, 0.1) 0px 4px 6px -1px, rgba(0, 0, 0, 0.06) 0px 2px 4px -1px;

  @include media("<desktop") {
    display: flex;
    flex-direction: column;
    padding-left: 3%;
    padding-right: 3%;
  }
  @include media("<phone") {
    display: flex;
    flex-direction: column;
    grid-template-columns: 1fr;
    grid-template-rows: 2fr;
    padding-left: 10px;
    padding-right: 10px;
  }
}


.Content1 {
  display: grid;
  grid-template-columns: 500px 400px;

  padding-top: 3%;
  padding-bottom: 3%;
  color: $c-black;

  &__CL1 {

    @include media("<desktop") {
      display: flex;
      flex-direction: column;
    }
  }

  &__CL2 {
    font-family: $f-StixIT;
  }

  @include media("<desktop") {
    display: flex;
    flex-direction: column;
    padding-left: 3%;
    padding-right: 3%;
  }
  @include media("<phone") {
    display: flex;
    flex-direction: column;
    grid-template-columns: 1fr;
    grid-template-rows: 2fr;
    padding-left: 10px;
    padding-right: 10px;
  }
}

.BriefDL {

  display: grid;
  grid-template-columns: auto 10fr;
  font-size: 18px;
  padding-left: 3%;

  &__DT {
    font-family: $f-NemaME;
    padding-bottom: 7%;
  }

  &__DD {
    font-family: $f-NemaLI;
    margin: 0 0 0 1em;
    padding-left: 5%;
  }
}

.BL1 {
  display: grid;
  grid-template-columns: 400px 500px;
  padding-top: 3%;
  padding-bottom: 3%;

  &__CL1 {

  }

  &__CL2 {
  padding-left: 50px;
  }
  @include media("<desktop") {
    display: flex;
    flex-direction: column;
  }
  @include media("<phone") {
    display: flex;
    flex-direction: column;
    grid-template-columns: 1fr;
    grid-template-rows: 2fr;
    padding-left: 10px;
    padding-right: 10px;
  }

  &__BLtitle {
    font-family: $f-NfBL;
    color: $c-black;
    font-size: 3em;
  }


  &__BLtitle2 {
    font-family: $f-NfBL;
    color: $c-white;
    font-size: 3em;
    // Removes the fill of the letter, gives only a contour
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    -webkit-text-stroke-width: 1px;
    -webkit-text-stroke-color: $c-black;
  }

}

.DL {
  border-bottom: 2px solid yellow;
  @include media("<desktop") {
    text-align: center;
  }
}

.DT {
  font-family: $f-NemaME;
  font-size: 25px;

  @include media("<desktop") {
    display: flex;
    justify-content: left;
    padding-left: 0;
  }
  
}

.DD {
  padding-top: 5px;
  margin-left: 10px;
  height: 25px;
  
}

.DDT {
  font-family: $f-NemaLI;
  font-size: 18px;
  padding-left: 5px;
}

.bigLine {
  border-bottom: 5px black solid;
}

.section2 {
  margin-top: 2%;
  margin-bottom: 10%;
  border-radius: 20px;
  box-shadow: rgba(0, 0, 0, 0.1) 0px 4px 6px -1px, rgba(0, 0, 0, 0.06) 0px 2px 4px -1px;
}


.Content2 {
  display: grid;
  grid-template-columns: 500px 400px;

  padding-top: 3%;
  padding-bottom: 3%;
  color: $c-black;

  &__CL1 {
    font-size: 24px;
    padding-left: 3%;
  }

  &__CL2 {
    font-family: $f-StixIT;
  }

  @include media("<desktop") {
    // display: none;
  }
  @include media("<phone") {
    display: grid;
    grid-template-columns: 1fr;
    grid-template-rows: 2fr;
    padding-left: 10px;
    padding-right: 10px;
  }
}
