// SCSS Configuration for the 'BasicCourse' page 

// Additional
@import '../../../styles/main';
@import '../../../styles/variables';

.Banner {
  background-color: $c-white;
  display: flex;
  flex-direction: column;
  justify-content: center;
  border-radius: 50px;

  @include media("<desktop") {
    display: flex;
    flex-direction: column;
  }
}

.CrsLogoSpace {
  display: flex;
  justify-content: center;
  padding-bottom: 10px;
}


.CrsLogo {
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #045de9;
  background-image: linear-gradient(315deg, #045de9 0%, #09c6f9 74%);
  width: 60%;
  height: 100%;
  border-radius: 4em 8em;
  opacity: 0.9;

  &__CN1 {
    font-family: $f-NfBL;
    color: $c-white;
    font-size: 2em;
  }

  &__CN2 {
    font-family: $f-NfBL;
    color: $c-white;
    font-size: 2em;
    // Removes the fill of the letter, gives only a contour
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    -webkit-text-stroke-width: 1px;
    -webkit-text-stroke-color: $c-white;
  }

  @include media("<desktop") {
    width: 100%;
  }
}

.CrsBannerCol {
  display: flex;
  justify-content: center;
  padding-left: 3%;
}

.bdl {
  display: grid;
  font-size: 18px;
  grid-template-columns: 200px 800px;
  // padding-left: 3%;

  @include media("<desktop") {
    display: grid;
    text-align: center;
    grid-template-columns: 1fr;
    grid-template-rows: 2fr;
    padding-left: 10px;
    padding-right: 10px;
  }
  @include media("<phone") {
    display: grid;
    text-align: center;
    grid-template-columns: 1fr;
    grid-template-rows: 2fr;
    padding-left: 10px;
    padding-right: 10px;
  }
}

.bdt {
  padding-bottom: 7%;
  font-family: $f-NemaBD;

  @include media("<desktop") {
    padding-bottom: 10px;
  }
  @include media("<phone") {
    padding-bottom: 10px;
  }
}

.bdd {
  margin: 0 0 0 1em;
  margin-left: 0;

  @include media("<desktop") {
    margin-left: 0;
    padding-bottom: 20px;
  }
  @include media("<phone") {
    margin-left: 0;
    padding-bottom: 20px;
  }
}

// ======================================

.CrsDesc {
  margin-top: 30px;
  border-radius: 50px;
  background-color: $c-white;
}

.CrsDecCol {
  display: grid;
  grid-template-columns: 400px 500px;
  padding-left: 6%;
  padding-top: 3%;
  padding-bottom: 3%;

  @include media("<desktop") {
    display: grid;
    grid-template-columns: 1fr;
    grid-template-rows: 2fr;
    padding-left: 20px;
    padding-right: 20px;
  }
  @include media("<phone") {
    display: grid;
    grid-template-columns: 1fr;
    grid-template-rows: 2fr;
    padding-left: 20px;
    padding-right: 20px;
  }
}

.CrsDecTitle {
  font-family: $f-NfBL;
  color: $c-black;
  font-size: 3em;

  &__u2 {
    font-family: $f-NfBL;
    font-size: 3em;
    // Removes the fill of the letter, gives only a contour
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    -webkit-text-stroke-width: 1px;
    -webkit-text-stroke-color: $c-black;
  }
}


.CrsDecsIcon {
  color: $c-light-grey;
  margin-right: 5px;
}

.dl {
  margin-left: 10%;
  margin-top: 0;
  font-size: 18px;
  @include media("<desktop") {
    margin-top: 3%;
    margin-left: 0%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    grid-template-columns: 1fr;
    grid-template-rows: 2fr;
  }
  @include media("<phone") {
    display: flex;
    flex-direction: column;
    justify-content: center;
    grid-template-columns: 1fr;
    grid-template-rows: 2fr;
  }
}

.dt {
  font-family: $f-NemaBD;
  padding-bottom: 10px;
}

.dd {
  padding-top: 5px;
  margin-left: 0;
  // height: 25px;
}
