@import './variables';
@import './media';

body {
  padding-top: 25px;
  padding-bottom: 0px;
  padding-left: 50px;
  padding-right: 50px;

  // background-color: #242325;
  background-color: whitesmoke;

  font-size: 16px;
  font-family: $f-NemaRE;
  color: $c-black;

  @include media("<desktop") {
    padding-top: 0px;
    padding-bottom: 0px;
    padding-left: 0px;
    padding-right: 0px;
  }
  @include media("<phone") {
    padding-top: 0px;
    padding-bottom: 0px;
    padding-left: 0px;
    padding-right: 0px;
  }
}

.wrapper {
  max-width: 1100px;
  padding-top: 10px;
  padding-bottom: 10px;
  padding-left: 50px;
  padding-right: 50px;
  
  // padding: 0 50px;

  margin: 0 auto;
  width: 100%;

  @include media("<desktop") {
    padding-left: 0px;
    padding-right: 0px;
  }
  
  @include media("<phone") {
    padding-left: 0px;
    padding-right: 0px;
  }
}

*::selection {
  background-color: $c-selection-f;
  color: $c-selection-t;
}

// TODO Scrollbar,
// *::-webkit-scrollbar 
// *::-webkit-scrollbar-track
// *::-webkit-scrollbar-thumb

a {
  text-decoration: none;
  &:hover {
    background-color: $c-var-a_HoverBackground;
    color: $c-var-a-HoverText;
  }
}

*::-webkit-scrollbar {
  width: 10px;
}

*::-webkit-scrollbar-track {
  -webkit-box-shadow: 5px 5px 5px -5px rgba(34, 60, 80, 0.2) inset;
  background-color: #ffffff;
  background-image: linear-gradient(315deg, #ffffff 0%, #d7e1ec 74%);
  border-radius: 50px;
}

*::-webkit-scrollbar-thumb {
  border-radius: 50px;
  background-color: white;
}