// SCSS Configuration for the 'Education' page 

// Additional
@import '../../styles/main';
@import '../../styles/variables';

.MainBanner {
  display: flex;
  flex-direction: column;
  background-color: #f9ea8f;
  background-image: linear-gradient(315deg, #f9ea8f 0%, #aff1da 74%);
  width: 100%;
  max-width: 100%;
  height: 300px;
  color: $c-black;
  border-radius: 30px 30px 0px 0px;

  @include media("<desktop") {
    // display: none;
  }
  @include media("<phone") {
    border-radius: 0;
  }
}

.MainBannerTitle {
  font-size: 5em;
  padding-bottom: 50px;
  color: $c-black;
  padding-top: 1%;
  padding-left: 5%;

  &__t1 {
    font-family: $f-NfRE;
    // Removes the fill of the letter, gives only a contour
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    -webkit-text-stroke-width: 0.9px;
    -webkit-text-stroke-color: $c-black;
  }
  
  &__t2 {
    font-family: $f-NfBL;
  }

  @include media("<desktop") {
    font-size: calc(24px + 30 * (100vw / 1280));
    padding-top: 10px;
  }
  @include media("<phone") {
    font-size: calc(24px + 30 * (100vw / 1280));
    padding-top: 10px;
  }
}

.MainBannerImg {
  position: absolute;
  right: 220px;
  top: 110px;
  height: 200px;
  width: 200px;
  opacity: 0.3;
  transform: rotate(-20deg);

  @include media("<desktop") {
    display: none;
  }
  @include media("<phone") {
    display: none;
  }
}

.MainBannerBriefSection {
  padding-left: 5%;
}

.MainBannerBriefUnit {
  display: flex;
  align-items: center;
  padding-bottom: 13px;
  font-size: 1.4em;

  &__Icon {
    color: white;
  }

  &__Text {
    font-family: $f-NemaRE;
    padding-left: 15px;
  }
}

