// SCSS Configuration for Contact Form

// Additional
@import '../../../styles/main';
@import '../../../styles/variables';

.BtnClose {
  float: left;
  transition: 0.2s ease;
  border-top: none;
  border-left: none;
  border-right: none;
  border-bottom: 1px solid #DDDDDD;
  border-radius: 20px;
  width: 350px;
  cursor: pointer;
  height: 35px;
  background-color: #eeeeee;
  moz-transition: all .4s ease-in-out;
  -o-transition: all .4s ease-in-out;
  -webkit-transition: all .4s ease-in-out;
  transition: all .4s ease-in-out;

  @include media("<desktop") {
    width: 80%;
  }
}

.form {
  // margin-right: -10%;
  // background: #1B1B1B;
  width: 100%;
  display: flex;
  flex-direction: column;

  // @include media("<desktop") {
  //   display: flex;
  //   flex-direction: column;
  //   justify-content: center;
  // }


}

.formgroup {
  position: relative;
  margin-bottom: 25px;
}


.title {
  font-family: $f-NemaBD;
  margin-bottom: 30px;
}

.highlight {
  position: absolute;
  height: 60%; 
  width: 100px; 
  top: 25%; 
  left: 0;
  pointer-events: none;
  // opacity: 0.5;
}

.label {
  color: $c-black;
  font-family: $f-StixIT;
  font-size: 13px;
  font-weight: normal;
  position: absolute;
  pointer-events: none;
  left: 5px;
  top: 15px;
  opacity: 0.5;
  transition: 0.2s ease all; 
  -moz-transition: 0.2s ease all; 
  -webkit-transition: 0.2s ease all;
}

.input {
  font-size: 13px;
  height: 25px;
  padding: 10px 10px 10px 5px;
  display: block;
  width: 350px;
  border: none;
  outline: none;
  border-bottom: 1px solid #B1B1B1;
  color: $c-black;
  background: rgba(0,0,0,0);
  // opacity: 0.5;
  transition: 0.2s ease;

  @include media("<desktop") {
    width: 80%;
  }
}

.input:focus {
  outline: none;
  opacity: 1;
}

// Setup for Text Area

.textarea {
  resize: vertical;
  width: 350px;
  height: 100px;
  font-size: 13px;
  padding: 10px 10px 10px 5px;
  display: block;
  border: none;
  outline: none;
  border-bottom: 1px solid #B1B1B1;
  color: $c-black;
  background: rgba(0,0,0,0);
  // opacity: 0.5;
  transition: 0.2s ease;

  @include media("<desktop") {
    width: 80%;
  }
}

.textarea:focus {
  outline: none;
  opacity: 1;
}

// Active State Setup

.input:focus ~ .label {
  top: -10px;
  font-size: 12px;
  color: #B1B1B1;
}

.textarea:focus ~ .label {
  top: -10px;
  font-size: 12px;
  color: #B1B1B1;
}

.textarea:focus ~ .label {
  top: -10px;
  font-size: 12px;
  color: #B1B1B1;
}

.input:valid ~ .label {
  top: -10px;
  font-size: 12px;
  color: #B1B1B1;
}

.input:focus ~ .highlight {
  // -webkit-animation:inputHighlighter 0.3s ease;
  // -moz-animation:inputHighlighter 0.3s ease;
  // animation:inputHighlighter 0.3s ease;
}

// Animation Setup
@-webkit-keyframes inputHighlighter {
  from { background:#B1B1B1; }
  to 	{ width:0; background:transparent; }
}
@-moz-keyframes inputHighlighter {
  from { background:#B1B1B1; }
  to 	{ width:0; background:transparent; }
}
@keyframes inputHighlighter {
  from { background:#B1B1B1; }
  to 	{ width:0; background:transparent; }
}


// Button Setup

.buttonsSection {
  display: flex;
  flex-direction: column;
  gap: 10px;
}

.button {
  float: left;
  transition: 0.2s ease;
  border-top: none;
  border-left: none;
  border-right: none;
  border-bottom: 1px solid #DDDDDD;
  border-radius: 20px;
  width: 350px;
  cursor: pointer;
  height: 35px;
  background-color: #eeeeee;
  moz-transition: all .4s ease-in-out;
  -o-transition: all .4s ease-in-out;
  -webkit-transition: all .4s ease-in-out;
  transition: all .4s ease-in-out;

  @include media("<desktop") {
    width: 80%;
  }

}

.button:hover {
  color: $c-black;
  cursor: pointer;
  background-color: #fce043;
  background-image: linear-gradient(315deg, #fce043 0%, #fb7ba2 74%);
  moz-transition: all .4s ease-in-out;
  -o-transition: all .4s ease-in-out;
  -webkit-transition: all .4s ease-in-out;
  transition: all .4s ease-in-out;
  box-shadow: rgba(0, 0, 0, 0.1) 0px 4px 6px -1px, rgba(0, 0, 0, 0.06) 0px 2px 4px -1px;
}

.buttonReset {
  float: left;
  transition: 0.2s ease;
  border-top: none;
  border-left: none;
  border-right: none;
  border-bottom: 1px solid #DDDDDD;
  border-radius: 20px;
  width: 200px;
  cursor: pointer;
  height: 25px;
  background-color: #eeeeee;
  moz-transition: all .4s ease-in-out;
  -o-transition: all .4s ease-in-out;
  -webkit-transition: all .4s ease-in-out;
  transition: all .4s ease-in-out;
  opacity: 0.2;
}

.buttonReset:hover {
  opacity: 0.9;
  color: $c-black;
  cursor: pointer;
  moz-transition: all .4s ease-in-out;
  -o-transition: all .4s ease-in-out;
  -webkit-transition: all .4s ease-in-out;
  transition: all .4s ease-in-out;
  box-shadow: rgba(0, 0, 0, 0.1) 0px 4px 6px -1px, rgba(0, 0, 0, 0.06) 0px 2px 4px -1px;
}