// SCSS Configuration for Header

// Additional
@import '../styles/variables';
@import '../styles/media';
@import '../styles/index.scss';


// Wrapper
.headerWrapper {
  display: flex;
  justify-content: space-between;
  width: 1450px;
  max-width: 100%;
  height: 50px;
  align-items: center;
  background-color: $c-black;
  border-radius: 50px 50px 50px 50px;
  z-index: 90;
  margin: auto;
  position: sticky;
  top: 0;

  @include media("<desktop") {
    // display: none;
  }
  @include media("<phone") {
    border-radius: 0;
  }

  // opacity:0; /*   ()*/
  // transition: 1s; /*   */
  // animation: show 1s 1; /*   ,     */
  // animation-fill-mode: forwards; /*        */
  // animation-delay: 0.5s; /*    */

  // @keyframes show{
  // 0%{
  // opacity:0;
  // }
  // 100% {
  // opacity:1;
  // }
  // }

}

// Logo
.headerLogo {
  justify-content: left;
  align-items: center;
  left: 0;
  font-family: $f-AltaCA;
  font-size: 1.3em;
  text-decoration: none;
  color: $c-white;
  padding-left: 30px;
}

.headerNavWrapper {
  display: flex;
}

.headerNav {
  display: flex;
  list-style-type: none;
  padding-right: 30px;
  

  // @include media("<phone") {
  //   display: none;
  // }

}

.headerNavLink {
  margin-left: 30px;
  text-decoration: none;
  font-family: $f-NemaRE;
  font-size: 0.9em;
  text-decoration: none;
  color: $c-white;
  padding: 10px;

  &__Active:active {
    color: greenyellow;
  }

  @include media("<phone") {
    display: none;
  }
}

.headerNavLink:hover {
  color: #ff7a59;
  background:#fff;
}